      }

      if(Util.Jia(this.pay.Money,this.pay.Point)<Util.Jia(this.orderApi.order.rmb,0))
      {
        this.pay.OnLine=Util.Jian(Util.Jian(this.orderApi.order.rmb,this.pay.Point),this.pay.Money);
      }

    }
  },
